<template>
    <div class='coachlist'>
        <div class='top'>
          <div class='title'>排课信息 <span>{{total}}</span></div>
          <div class='btn' @click='member'><i class='iconfont icon-btn_icon_add'></i>添加排课</div>
        </div>
        <Table  :tableData="tableData" :tableQuery="tableQuery" :tabhide="tabhide" :tableKey="tableKey" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize" v-on:getQueryParam = "getQueryParam"
       v-on:changeVal="changeParam" v-on:getRowdata="getRowdata" :operateBtn="operateBtn"></Table>
    </div>
</template>
<script>
import Table from '../../components/Table'
import {queryScheduleCourse, deleteScheduleCourse} from '../../services/service.js'
import {isApiResponseOk, setSessionStorage} from '../../utils/com.js'
export default {
  data () {
    return {
      tableQuery: [
        {
          name: 'Week',
          type: 'select',
          model: '',
          title: '请选择',
          text: '星期：',
          selectArr: []
        },
        {
          name: 'Professional',
          type: 'select',
          model: '',
          title: '请选择',
          text: '专业：',
          selectArr: []
        }
      ],
      tableKey: [
        {name: '课程名称', value: 'courseName', nor: true},
        {name: '专业', value: 'professionalName', nor: true},
        {name: '指导老师', value: 'name', nor: true},
        {name: '星期', value: 'weekName', nor: true},
        {name: '上课时间', value: 'time', nor: true},
        {name: '开课状态', value: 'isActive', nor: true}
      ],
      tableData: [],
      isPage: true,
      total: 0,
      tabhide: false,
      pageSize: 10,
      para: {currentPage: 1, Week: '', Professional: '',pageSize: 10},
      operateBtn: [{text: '详情'},{text: '删除'}]
    }
  },
  components: {
    Table
  },
  created () {
    // 获取学员列表
    this.$nextTick(_ => {
      this.listSelect()
      this.initStudent(this.para)
    })
  },
  methods: {
  	listSelect () {
      let option1 = [
        {
          value: 1,
          label: '计算机专业'
        },
        {
          value: 2,
          label: '英文专业'
        },
        {
          value: 3,
          label: '中文专业'
        },
        {
          value: 4,
          label: '数学专业'
        },
        {
          value: 5,
          label: '医学专业'
        }
      ]
      let option2= [
        {
          value: 1,
          label: '周一'
        },
        {
          value: 2,
          label: '周二'
        },
        {
          value: 3,
          label: '周三'
        },
        {
          value: 4,
          label: '周四'
        },
        {
          value: 5,
          label: '周五'
        },
        {
          value: 6,
          label: '周六'
        },
        {
          value: 7,
          label: '周日'
        }
      ]
      this.$refs.Table.reloadValue('Professional', option1)
      this.$refs.Table.reloadValue('Week', option2)
    },
    getQueryParam (queryObj) {
      // this.currentList = []
      // this.IndexLsit = []
      console.log(queryObj)
      this.para.currentPage = 1
      this.para.Professional = queryObj.Professional
      this.para.Week = queryObj.Week
      this.initStudent(this.para)
    },
    changeParam (data) {
      this.para.currentPage = data
      this.initStudent(this.para)
    },
    getRowdata (data) {
    	console.log(data)
    	if (data.index == '详情') {
    		setSessionStorage('detailInfo', data.row)
    		this.$router.push({name: 'selectCourseDetail', query: {btnType: 'edit'}})
    	}
    	if (data.index == '删除') {
    		deleteScheduleCourse(data.row.id).then(function (res) {
    			if (res.code == 0) {
    				that.$message({
	    				showClose: true,
						message: '删除成功',
						type: 'success'
	    			})
    				that.initStudent(that.para)
    			} else {
    				that.$message({
	    				showClose: true,
						message: res.message,
						type: 'warning'
	    			})
    			}
    		})
    	}
    },
    initStudent (data) {
    	let that = this
    	queryScheduleCourse(data).then(function (res){
    		if (res && res.code == 0) {
    			let list = res.data.data
    			for (var i=0; i<list.length; i++) {
    				console.log(list[i].course)
    				for (var key in list[i].course) {
    					if (key == 'courseName') {
    						list[i][key] = list[i].course[key]
    					}
    				}
    				for (var key2 in list[i].teachers.users) {
    					if (key2 == 'name') {
    						list[i][key2] = list[i].teachers.users[key2]
    					}
    				}
    				for (var key3 in list[i].teachers) {
    					if (key3 == 'professionalName') {
    						list[i][key3] = list[i].teachers[key3]
    					}
    				}
    			}
    			that.tableData = list
          		that.total = res.data.totalCount
          		if (that.total === 0) {
		            that.isPage = false
		        } else {
		            that.isPage = true
		        }
    		} else {
    			that.isPage = false
    			that.$message({
    				showClose: true,
					message: res.message,
					type: 'warning'
    			})
    		}
    	})
    },
    // 导入会员
    member () {
    	this.$router.push({name: 'selectCourseDetail', query: {btnType: 'add'}})
    }
  }
}
</script>
<style lang="sass" scoped>
.coachlist
  .top
    display: flex;
    height: .36rem;
    margin-bottom: .3rem;
    .btn
      font-family: PingFangSC-Medium;
      font-size: .15rem;
      color: #20C6BA;
      letter-spacing: 0;
      line-height: .15rem;
      height: .36rem;
      line-height: .36rem;
      border: 1px solid #20C6BA;
      border-radius: .02rem;
      width: 1.32rem;
      text-align: center;
      cursor: pointer;
    .title
      flex: 1;
      line-height: .36rem;
      span
        background: #E9EDEF;
        border-radius: 100px;
        font-size: .2rem;
        color: #8597A1;
        padding: .03rem .14rem;
        margin-left: .1rem;
    .icon-btn_icon_input:before
      margin-right: .12rem;
    .icon-btn_icon_add:before
    	margin-right: .12rem;  
</style>




